.estimate {
  padding: 30px 0;
  @include screen(laptop) {
    padding: 60px 0;
  }
  &__wrapper {
    background-color: $dark;
    border-radius: 10px;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    display: grid;
    padding: 30px 24px;
    align-items: flex-end;
    @include screen(laptop) {
      gap: 32px;
      padding: 80px 45px;
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__form {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    display: grid;
    gap: 16px;
    margin-top: 24px;
    @include screen(laptop) {
      margin-top: 0;
      grid-template-columns: repeat(3, minmax(0, 1fr));
      grid-column: span 8 / span 8;
    }
  }
  &__title {
    font-weight: 500;
    letter-spacing: -0.01em;
    font-size: 32px;
    line-height: 35px;
    max-width: 269px;
    color: $white;
    @include screen(laptop) {
      font-size: 50px;
      max-width: 356px;
      line-height: 55px;
      grid-column: span 4 / span 4;
    }
  }
  &__input {
    border: 1px solid rgba(#D1D1D1, 0.3);
    background-color: rgba($white, 0.06);
    border-radius: 6px;
    font-size: 16px;
    line-height: 22px;
    max-height: 74px;
    padding: 16px 29px;
    color: $white;
    width: 100%;
    @include screen(laptop) {
      padding: 26px;
    }
  }
}