.calculator {
  background-color: #E8EBF1;
  padding: 30px 0;
  @include screen(laptop) {
    padding: 60px 0;
  }
  &__container {
    background-color: $white;
    display: grid;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    margin-top: 24px;
    @include screen(laptop) {
      grid-template-columns: repeat(12, minmax(0, 1fr));
      margin-top: 48px;
    }
  }
  &__bg {
    display: none;
    @include screen(laptop) {
      display: block;
      grid-column: span 8 / span 8;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__form {
    z-index: 3;
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 24px 10px;
    align-items: flex-start;
    gap: 32px;
    @include screen(laptop) {
      padding: 32px 30px;
      grid-column: span 4 / span 4;
    }
  }
  &__title {
    font-size: 22px;
    line-height: 26px;
    font-weight: 500;
    letter-spacing: -0.01em;
    grid-column: span 2 / span 2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 6px;
    width: 100%;
    @include screen(laptop) {
      font-size: 24px;
      line-height: 29px;
    }
  }
  &__block {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 10px;
  }
  &__price {
    font-size: 20px;
    line-height: 24px;
    font-weight: 700;
    letter-spacing: -0.01em;
    border-bottom: 3px solid $primary;
    display: flex;
    align-items: center;
    @include screen(laptop) {
      line-height: 29px;
      font-size: 24px;
    }
    svg {
      width: 10px;
    }
  }
  &__footer {
    gap: 0;
  }
  &__button {
    margin-top: 20px;
    display: flex;
    align-items: center;
  }
  &__icon {
    position: absolute;
    left: 13px;
  }
  &__label {
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    &-checkbox {
      grid-column: span 2 / span 2;
      cursor: pointer;
    }
    input[type="checkbox"] {
      position: absolute;
      width: 0;
      height: 0;
      visibility: hidden;
      opacity: 0;
      &:checked ~ p {
        border-color: $dark;
        &:after {
          opacity: 1;
        }
      }
    }
    input[type="text"] {
      border: 1px solid #E5E5E5;
      border-radius: 8px;
      padding: 11px 50px 11px 42px;
      width: 100%;
    }
    p {
      border: 1px solid #E5E5E5;
      padding: 11px 14px 11px 42px;
      border-radius: 8px;
      width: 100%;
      transition: border-color $transition-default;
      display: flex;
      align-items: center;
      &:before {
        content: '';
        position: absolute;
        left: 16px;
        min-width: 16px;
        min-height: 16px;
        border-radius: 50%;
        border: 1px solid $dark;
      }
      &:after {
        content: '';
        position: absolute;
        left: 19px;
        min-width: 10px;
        min-height: 10px;
        background-color: $dark;
        border-radius: 50%;
        border: 1px solid $dark;
        opacity: 0;
        transition: opacity $transition-default;
      }
    }
    span {
      right: 13px;
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      letter-spacing: -0.01em;
      color: rgba($dark, 0.2);
      position: absolute;
    }
  }
  &__input {
    width: 100%;
  }
  &__button {
    grid-column: span 2 / span 2;
  }
}