.why {
  background-color: #E8EBF1;
  padding: 30px 0 0;
  @include screen(laptop) {
    padding: 60px 0 0;
  }
  &__bg {
    position: fixed;
    inset: -9999px;
    background-color: rgba(#222222, 0.8);
    z-index: 13;
    opacity: 0;
    visibility: hidden;
    transition: opacity $transition-default, visibility $transition-default;
    @include screen(laptop) {
      display: none;
    }
    &.active {
      opacity: 1;
      visibility: visible;
    }
  }
  &__wrapper {
    border-bottom: 1px solid $dark;
    position: relative;
    padding: 0 0 30px;
    max-width: 640px;
    margin: 0 auto;
    @include screen(laptop) {
      padding: 0 0 60px;
      max-width: 100%;
    }
  }
  &__buttons {
    display: inline-flex;
    margin-top: 20px;
    width: 100%;
    overflow-x: auto;
    align-items: flex-start;
    gap: 9px;
    @include screen(laptop) {
      margin-top: 127px;
      background-color: $white;
      width: auto;
      overflow: hidden;
      border: 1px solid #E5E5E5;
      max-width: 100%;
      border-radius: 10px;
      padding: 9px;
      flex-direction: column;
    }
  }
  &__button {
    text-transform: uppercase;
    padding: 17px 23px;
    border-radius: 6px;
    text-align: center;
    font-size: 16px;
    white-space: nowrap;
    background-color: rgba($white, 0.4);
    line-height: 19px;
    font-weight: 500;
    width: 100%;
    transition: color $transition-default, background-color $transition-default;
    &.active {
      color: $white;
      background-color: $primary;
    }
    &:hover {
      color: $white;
      background-color: $primary;
    }
    @include screen(laptop) {
      background-color: transparent;
      font-size: 24px;
      text-align: left;
      line-height: 20px;
    }
  }
  &__blocks {
    margin-top: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    @include screen(laptop) {
      position: absolute;
      display: block;
      margin-top: 0px;
      top: 0;
      right: 200px;
    }
  }
  &__popup {
    cursor: pointer;
    position: fixed;
    z-index: 12;
    @include screen(laptop) {
      z-index: 4;
      position: absolute;
    }
    &.active {
      z-index: 13;
    }
  }
  &__switcher {
    z-index: 3;
    position: relative;
    top: 10px;
    right: 10px;
    transition: transform $transition-default;
    svg {
      width: 48px;
      height: 48px;
      @include screen(laptop) {
        width: 60px;
        height: 60px;
      }
      rect {
        @include screen(laptop) {
          transition: fill $transition-default;
        }
      }
    }
    &.active {
      @include screen(laptop) {
        transform: rotate(45deg);
        svg {
          rect {
            fill: #F2F2F2;
          }
        }
      }
    }
  }
  &__close {
    cursor: pointer;
    position: absolute;
    right: 10px;
    top: 10px;
    @include screen(laptop) {
      display: none;
    }
  }
  &__containers {
    @include screen(laptop) {
      display: none;
    }
  }
  &__container {
    display: none;
    opacity: 0;
    background-color: $white;
    padding: 96px 20px 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 6px -2px #0000000D, 0px 10px 15px -3px #0000001A;
    position: fixed;
    width: 100%;
    z-index: -1;
    max-width: 360px;
    transform: translate(50%, -50%);
    top: 50%;
    right: 50%;
    border: 1px solid rgba($dark, 0.4);
    @include screen(laptop) {
      transition: opacity $transition-default, visibility $transition-default, z-index $transition-default;
      display: block;
      position: absolute;
      padding: 53px 40px 40px 30px;
      transform: translate(0);
      min-width: 567px;
      right: 0;
      top: 0;
      visibility: hidden;
      border: none;
      max-width: 567px;
    }
    &.active {
      opacity: 1;
      visibility: visible;
    }
    h3 {
      font-weight: 500;
      font-size: 22px;
      line-height: 26px;
      text-align: left;
      margin-bottom: 16px;
      @include screen(laptop) {
        margin-bottom: 24px;
        line-height: 43px;
        font-size: 36px;
      }
    }
    p {
      font-weight: 500;
      font-size: 16px;
      line-height: 22px;
      letter-spacing: -0.01em;
      text-align: left;
      @include screen(laptop) {
        line-height: 29px;
        font-size: 24px;

      }
    }
  }
  &__block {
    display: none;
    position: relative;
    transform: scale(0.9);
    @include screen(mobile-sm) {
      transform: scale(1);
    }
    &.active {
      display: block;
    }
    &-1 {
      .why__popup-1 {
        top: 150px;
        left: 100px;
        @include screen(laptop) {
          top: 160px;
          left: 100px;
        }
      }
      .why__popup-2 {
        top: 50px;
        left: 240px;
        @include screen(laptop) {
          top: 100px;
          left: 370px;
        }
      }
      .why__popup-3 {
        top: 290px;
        left: 100px;
        @include screen(laptop) {
          top: 450px;
          left: 150px;
        }
      }
      .why__popup-4 {
        top: 230px;
        left: 200px;
        @include screen(laptop) {
          top: 350px;
          left: 370px;
        }
      }
    }
    &-2 {
      .why__popup-1 {
        top: 150px;
        left: 100px;
        @include screen(laptop) {
          top: 180px;
          left: 150px;
        }
      }
      .why__popup-2 {
        top: 50px;
        left: 240px;
        @include screen(laptop) {
          top: 100px;
          left: 350px;
        }
      }
      .why__popup-3 {
        top: 290px;
        left: 100px;
        @include screen(laptop) {
          top: 450px;
          left: 150px;
        }
      }
      .why__popup-4 {
        top: 230px;
        left: 200px;
        @include screen(laptop) {
          top: 350px;
          left: 350px;
        }
      }
    }
    &-3 {
      .why__popup-1 {
        top: 60px;
        left: 80px;
        @include screen(laptop) {
          top: 180px;
          left: 130px;
        }
      }
      .why__popup-2 {
        top: 110px;
        right: 0;
        @include screen(laptop) {
          top: 230px;
          right: 30px;
        }
      }
      .why__popup-3 {
        top: 170px;
        left: 0;
        @include screen(laptop) {
          top: 340px;
          left: 150px;
        }
      }
      .why__popup-4 {
        top: 220px;
        right: 80px;
        @include screen(laptop) {
          bottom: 140px;
          top: auto;
          right: 150px;
        }
      }
    }
  }
  &__image {
    img {
      max-width: 360px;
      max-height: 360px;
      @include screen(laptop) {
        max-width: 650px;
        max-height: 650px;
      }
      @include screen(desktop) {
        max-width: 746px;
        max-height: 746px;
      }
    }
  }
  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: column;
    gap: 30px;
    margin-top: 43px;
    @include screen(laptop) {
      margin-top: 227px;
      flex-direction: row;
      align-items: center;
    }
  }
  &__tip {
    max-width: 352px;
    font-weight: 500;
    letter-spacing: -0.01em;
    font-size: 22px;
    line-height: 26px;
    @include screen(laptop) {
      line-height: 29px;
      font-size: 24px;
    }
  }
  &__calculate {
    width: auto;
  }
}