.callback {
  padding: 30px 0;
  @include screen(laptop) {
    padding: 60px 0;
  }
  &__wrapper {
    display: flex;
    gap: 32px;
    flex-direction: column;
    @include screen(laptop) {
      flex-direction: row;
    }
  }
  &__image {
    flex: 1;
  }
  &__form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 16px;
    @include screen(laptop) {
      max-width: 511px;
      gap: 77px;
    }
  }
  &__labels {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }
  &__input {
    border: 1px solid #D1D1D1;
    font-size: 16px;
    font-weight: 500;
    line-height: 22.4px;
    letter-spacing: -0.01em;
    color: $dark;
    width: 100%;
    border-radius: 8px;
    padding: 16px 29px;
    @include screen(laptop) {
      padding: 26px;
    }
  }
  &__image {
    border-radius: 10px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}